import React, { forwardRef, useId } from "react";

interface InputProps extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  error?: string;
  helpText?: string;
  fullWidth?: boolean;
  startIcon?: React.ReactNode;
  endIcon?: React.ReactNode;
}

const Input = forwardRef<HTMLInputElement, InputProps>(
  (
    {
      label,
      error,
      helpText,
      fullWidth = false,
      startIcon,
      endIcon,
      className = "",
      disabled = false,
      ...props
    },
    ref,
  ) => {
    const uniqueId = useId();
    const inputId = props.id || uniqueId;

    const baseClasses =
      "px-3 py-2 border rounded-md text-sm transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed";

    const variantClasses = error
      ? "border-red-500 focus:border-red-500 focus:ring-red-500"
      : "border-gray-300 dark:border-gray-600 focus:border-primary-400 focus:ring-primary-400";

    const backgroundClasses =
      "bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 placeholder:text-gray-500 dark:placeholder:text-gray-400";

    const widthClasses = fullWidth ? "w-full" : "";

    const paddingClasses = startIcon ? "pl-10" : endIcon ? "pr-10" : "";

    const combinedClasses = `${baseClasses} ${variantClasses} ${backgroundClasses} ${widthClasses} ${paddingClasses} ${className}`;

    return (
      <div className={fullWidth ? "w-full" : ""}>
        {label && (
          <label
            htmlFor={inputId}
            className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"
          >
            {label}
          </label>
        )}
        <div className="relative">
          {startIcon && (
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <span className="text-gray-400">{startIcon}</span>
            </div>
          )}
          <input
            ref={ref}
            id={inputId}
            className={combinedClasses}
            disabled={disabled}
            {...props}
          />
          {endIcon && (
            <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
              <span className="text-gray-400">{endIcon}</span>
            </div>
          )}
        </div>
        {error && (
          <p className="mt-1 text-sm text-red-600 dark:text-red-400">{error}</p>
        )}
        {helpText && !error && (
          <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
            {helpText}
          </p>
        )}
      </div>
    );
  },
);

Input.displayName = "Input";

export default Input;
