import React, { useState, useRef, useEffect } from "react";
import Icon from "./Icon";

interface HexImageProps extends Omit<React.ImgHTMLAttributes<HTMLImageElement>, 'src'> {
  hexData: string;
  alt: string;
  imageType?: "png" | "jpg" | "jpeg" | "webp" | "gif";
  fallbackSrc?: string;
  placeholder?: "blur" | "skeleton" | "none";
  aspectRatio?: "square" | "video" | "portrait" | "landscape" | "auto";
  objectFit?: "contain" | "cover" | "fill" | "none" | "scale-down";
  loading?: "lazy" | "eager";
  showLoadingSpinner?: boolean;
  showErrorIcon?: boolean;
  className?: string;
  containerClassName?: string;
  onLoad?: () => void;
  onError?: () => void;
}

const HexImage: React.FC<HexImageProps> = ({
  hexData,
  alt,
  imageType = "png",
  fallbackSrc,
  placeholder = "skeleton",
  aspectRatio = "auto",
  objectFit = "cover",
  loading = "lazy",
  showLoadingSpinner = true,
  showErrorIcon = true,
  className = "",
  containerClassName = "",
  onLoad,
  onError,
  ...props
}) => {
  const [imageState, setImageState] = useState<"loading" | "loaded" | "error">(
    "loading",
  );
  const [currentSrc, setCurrentSrc] = useState<string>("");
  const imgRef = useRef<HTMLImageElement>(null);

  useEffect(() => {
    const decodeHexImage = async () => {
      try {
        setImageState("loading");

        // Convert hex string to Uint8Array
        const bytes = new Uint8Array(
          hexData.match(/.{1,2}/g)?.map(byte => parseInt(byte, 16)) || []
        );

        // Determine MIME type based on imageType
        const mimeTypes = {
          png: 'image/png',
          jpg: 'image/jpeg',
          jpeg: 'image/jpeg',
          webp: 'image/webp',
          gif: 'image/gif',
        };

        // Check if data is gzip compressed (gzip magic bytes: 1f 8b)
        const isGzipCompressed = bytes[0] === 0x1f && bytes[1] === 0x8b;

        let imageData: ArrayBuffer;

        if (isGzipCompressed) {
          // Decompress using native DecompressionStream (modern browsers)
          const decompressedStream = new Response(
            new Response(bytes).body?.pipeThrough(new DecompressionStream('gzip'))
          );
          imageData = await decompressedStream.arrayBuffer();
        } else {
          // Use raw bytes directly (not compressed)
          imageData = bytes.buffer;
        }

        // Create blob and object URL
        const blob = new Blob([imageData], { type: mimeTypes[imageType] });
        const url = URL.createObjectURL(blob);

        setCurrentSrc(url);

        // Cleanup function will be handled in the cleanup useEffect
      } catch (err) {
        console.error('Hex image decoding error:', err);
        if (fallbackSrc) {
          setCurrentSrc(fallbackSrc);
        } else {
          setImageState("error");
          onError?.();
        }
      }
    };

    if (hexData) {
      decodeHexImage();
    }

    // Cleanup function to revoke object URL
    return () => {
      if (currentSrc && currentSrc.startsWith('blob:')) {
        URL.revokeObjectURL(currentSrc);
      }
    };
  }, [hexData, fallbackSrc, onError]);

  const handleLoad = () => {
    setImageState("loaded");
    onLoad?.();
  };

  const handleError = () => {
    if (fallbackSrc && currentSrc !== fallbackSrc) {
      setCurrentSrc(fallbackSrc);
      return;
    }
    setImageState("error");
    onError?.();
  };

  const aspectRatioClasses = {
    square: "aspect-square",
    video: "aspect-video",
    portrait: "aspect-[3/4]",
    landscape: "aspect-[4/3]",
    auto: "",
  };

  const objectFitClasses = {
    contain: "object-contain",
    cover: "object-cover",
    fill: "object-fill",
    none: "object-none",
    "scale-down": "object-scale-down",
  };

  const containerClasses = `relative overflow-hidden ${aspectRatioClasses[aspectRatio]} ${containerClassName}`;

  const imageClasses = ` ${objectFitClasses[objectFit]} ${className}`;

  const renderPlaceholder = () => {
    if (placeholder === "none" || imageState === "loaded") return null;

    if (placeholder === "blur") {
      return (
        <div className="absolute inset-0 bg-gradient-to-br from-gray-200 to-gray-300 dark:from-gray-700 dark:to-gray-800" />
      );
    }

    if (placeholder === "skeleton") {
      return (
        <div className="absolute inset-0 bg-gray-200 dark:bg-gray-700">
          <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent" />
        </div>
      );
    }

    return null;
  };

  const renderLoadingSpinner = () => {
    if (!showLoadingSpinner || imageState !== "loading") return null;

    return (
      <div className="absolute inset-0 flex items-center justify-center bg-gray-100/80 dark:bg-gray-800/80">
        <div className="rounded-full h-8 w-8 border-b-2 border-primary-400"></div>
      </div>
    );
  };

  const renderErrorState = () => {
    if (!showErrorIcon || imageState !== "error") return null;

    return (
      <div className="absolute inset-0 flex flex-col items-center justify-center bg-gray-100 dark:bg-gray-800 text-gray-500 dark:text-gray-400">
        <Icon name="alert" size="lg" className="mb-2" />
        <span className="text-xs text-center px-2">Failed to load hex image</span>
      </div>
    );
  };

  // Don't render the img element if we don't have a valid source yet
  if (!currentSrc && imageState !== "error") {
    return (
      <div className={containerClasses}>
        {renderPlaceholder()}
        {renderLoadingSpinner()}
      </div>
    );
  }

  return (
    <div className={containerClasses}>
      {renderPlaceholder()}

      {currentSrc && (
        <img
          ref={imgRef}
          src={currentSrc}
          alt={alt}
          loading={loading}
          className={imageClasses}
          onLoad={handleLoad}
          onError={handleError}
          {...props}
        />
      )}

      {renderLoadingSpinner()}
      {renderErrorState()}
    </div>
  );
};

export default HexImage;