import React from "react";
import Image from "./Image";

interface CardProps {
  children: React.ReactNode;
  variant?: "default" | "outlined" | "elevated";
  padding?: "none" | "sm" | "md" | "lg";
  className?: string;
  onClick?: () => void;
  hover?: boolean;
}

interface CardHeaderProps {
  children: React.ReactNode;
  className?: string;
}

interface CardBodyProps {
  children: React.ReactNode;
  className?: string;
}

interface CardFooterProps {
  children: React.ReactNode;
  className?: string;
}

interface CardImageProps {
  src: string;
  alt: string;
  fallbackSrc?: string;
  aspectRatio?: "square" | "video" | "portrait" | "landscape" | "auto";
  position?: "top" | "bottom" | "left" | "right";
  className?: string;
}

const Card: React.FC<CardProps> & {
  Header: React.FC<CardHeaderProps>;
  Body: React.FC<CardBodyProps>;
  Footer: React.FC<CardFooterProps>;
  Image: React.FC<CardImageProps>;
} = ({
  children,
  variant = "default",
  padding = "md",
  className = "",
  onClick,
  hover = false,
  ...props
}) => {
  const baseClasses = "rounded-lg transition-colors";

  const variantClasses = {
    default:
      "bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700",
    outlined: "bg-transparent border-2 border-gray-300 dark:border-gray-600",
    elevated:
      "bg-white dark:bg-gray-800 shadow-lg border border-gray-100 dark:border-gray-700",
  };

  const paddingClasses = {
    none: "",
    sm: "p-3",
    md: "p-4",
    lg: "p-6",
  };

  const interactiveClasses =
    onClick || hover
      ? "cursor-pointer hover:shadow-md hover:border-gray-300 dark:hover:border-gray-500"
      : "";

  const combinedClasses = `${baseClasses} ${variantClasses[variant]} ${paddingClasses[padding]} ${interactiveClasses} ${className}`;

  return (
    <div className={combinedClasses} onClick={onClick} {...props}>
      {children}
    </div>
  );
};

const CardHeader: React.FC<CardHeaderProps> = ({
  children,
  className = "",
}) => (
  <div
    className={`pb-3 border-b border-gray-200 dark:border-gray-700 ${className}`}
  >
    {children}
  </div>
);

const CardBody: React.FC<CardBodyProps> = ({ children, className = "" }) => (
  <div className={`py-3 ${className}`}>{children}</div>
);

const CardFooter: React.FC<CardFooterProps> = ({
  children,
  className = "",
}) => (
  <div
    className={`pt-3 border-t border-gray-200 dark:border-gray-700 ${className}`}
  >
    {children}
  </div>
);

const CardImage: React.FC<CardImageProps> = ({
  src,
  alt,
  fallbackSrc,
  aspectRatio = "video",
  position = "top",
  className = "",
}) => {
  const positionClasses = {
    top: "rounded-t-lg",
    bottom: "rounded-b-lg",
    left: "rounded-l-lg",
    right: "rounded-r-lg",
  };

  return (
    <div className={`overflow-hidden ${positionClasses[position]}`}>
      <Image
        src={src}
        alt={alt}
        {...(fallbackSrc && { fallbackSrc })}
        aspectRatio={aspectRatio}
        objectFit="cover"
        className={`w-full ${className}`}
        placeholder="skeleton"
        showLoadingSpinner={true}
        showErrorIcon={true}
      />
    </div>
  );
};

Card.Header = CardHeader;
Card.Body = CardBody;
Card.Footer = CardFooter;
Card.Image = CardImage;

export default Card;
