import React, { useState, useRef, useEffect } from "react";
import Icon from "./Icon";

interface ImageProps extends React.ImgHTMLAttributes<HTMLImageElement> {
  src: string;
  alt: string;
  fallbackSrc?: string;
  placeholder?: "blur" | "skeleton" | "none";
  aspectRatio?: "square" | "video" | "portrait" | "landscape" | "auto";
  objectFit?: "contain" | "cover" | "fill" | "none" | "scale-down";
  loading?: "lazy" | "eager";
  showLoadingSpinner?: boolean;
  showErrorIcon?: boolean;
  className?: string;
  containerClassName?: string;
  onLoad?: () => void;
  onError?: () => void;
}

const Image: React.FC<ImageProps> = ({
  src,
  alt,
  fallbackSrc,
  placeholder = "skeleton",
  aspectRatio = "auto",
  objectFit = "cover",
  loading = "lazy",
  showLoadingSpinner = true,
  showErrorIcon = true,
  className = "",
  containerClassName = "",
  onLoad,
  onError,
  ...props
}) => {
  const [imageState, setImageState] = useState<"loading" | "loaded" | "error">(
    "loading",
  );
  const [currentSrc, setCurrentSrc] = useState(src);
  const imgRef = useRef<HTMLImageElement>(null);

  useEffect(() => {
    setImageState("loading");
    setCurrentSrc(src);
  }, [src]);

  const handleLoad = () => {
    setImageState("loaded");
    onLoad?.();
  };

  const handleError = () => {
    if (fallbackSrc && currentSrc !== fallbackSrc) {
      setCurrentSrc(fallbackSrc);
      return;
    }
    setImageState("error");
    onError?.();
  };

  const aspectRatioClasses = {
    square: "aspect-square",
    video: "aspect-video",
    portrait: "aspect-[3/4]",
    landscape: "aspect-[4/3]",
    auto: "",
  };

  const objectFitClasses = {
    contain: "object-contain",
    cover: "object-cover",
    fill: "object-fill",
    none: "object-none",
    "scale-down": "object-scale-down",
  };

  const containerClasses = `relative overflow-hidden ${aspectRatioClasses[aspectRatio]} ${containerClassName}`;

  const imageClasses = ` ${objectFitClasses[objectFit]} ${className}`;

  const renderPlaceholder = () => {
    if (placeholder === "none" || imageState === "loaded") return null;

    if (placeholder === "blur") {
      return (
        <div className="absolute inset-0 bg-gradient-to-br from-gray-200 to-gray-300 dark:from-gray-700 dark:to-gray-800" />
      );
    }

    if (placeholder === "skeleton") {
      return (
        <div className="absolute inset-0 bg-gray-200 dark:bg-gray-700">
          <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent" />
        </div>
      );
    }

    return null;
  };

  const renderLoadingSpinner = () => {
    if (!showLoadingSpinner || imageState !== "loading") return null;

    return (
      <div className="absolute inset-0 flex items-center justify-center bg-gray-100/80 dark:bg-gray-800/80">
        <div className="rounded-full h-8 w-8 border-b-2 border-primary-400"></div>
      </div>
    );
  };

  const renderErrorState = () => {
    if (!showErrorIcon || imageState !== "error") return null;

    return (
      <div className="absolute inset-0 flex flex-col items-center justify-center bg-gray-100 dark:bg-gray-800 text-gray-500 dark:text-gray-400">
        <Icon name="alert" size="lg" className="mb-2" />
        <span className="text-xs text-center px-2">Failed to load image</span>
      </div>
    );
  };

  return (
    <div className={containerClasses}>
      {renderPlaceholder()}

      <img
        ref={imgRef}
        src={currentSrc}
        alt={alt}
        loading={loading}
        className={imageClasses}
        onLoad={handleLoad}
        onError={handleError}
        {...props}
      />

      {renderLoadingSpinner()}
      {renderErrorState()}
    </div>
  );
};

export default Image;
