import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Logo, Button, Icon } from "@/components/ui";
import HexImage from "@/components/ui/HexImage";

// Compressed hex encoded logo image (PNG format)
const logo_image =
  "";

interface HeaderProps {
  className?: string;
  isDark?: boolean;
  onThemeToggle?: () => void;
}

interface LanguageSwitcherProps {
  className?: string;
}

const LanguageSwitcher: React.FC<LanguageSwitcherProps> = ({
  className = "",
}) => {
  const { i18n } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);

  const languages = [
    { code: "en", name: "English", nativeName: "English" },
    { code: "hi", name: "Hindi", nativeName: "" },
    { code: "te", name: "Telugu", nativeName: "" },
  ];

  const currentLanguage =
    languages.find((lang) => lang.code === i18n.language) ?? languages[0];

  const handleLanguageChange = (langCode: string) => {
    i18n.changeLanguage(langCode);
    setIsOpen(false);
  };

  return (
    <div className={`relative ${className}`}>
      <Button
        variant="ghost"
        size="sm"
        onClick={() => setIsOpen(!isOpen)}
        className="flex items-center space-x-2"
      >
        <Icon name="document" size="sm" />
        <span className="hidden md:inline">{currentLanguage?.nativeName}</span>
        <Icon name="arrow-down" size="xs" />
      </Button>

      {isOpen && (
        <div className="absolute right-0 top-full mt-1 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg shadow-lg py-1 z-50 min-w-32">
          {languages.map((language) => (
            <button
              key={language.code}
              onClick={() => handleLanguageChange(language.code)}
              className={`w-full text-left px-3 py-2 text-sm hover:bg-gray-100 dark:hover:bg-gray-700 ${
                i18n.language === language.code
                  ? "bg-primary-50 text-primary-600 dark:bg-primary-900 dark:text-primary-300"
                  : "text-gray-700 dark:text-gray-300"
              }`}
            >
              <div className="flex flex-col">
                <span className="font-medium">{language.nativeName}</span>
                <span className="text-xs opacity-70">{language.name}</span>
              </div>
            </button>
          ))}
        </div>
      )}
    </div>
  );
};

const Header: React.FC<HeaderProps> = ({
  className = "",
  isDark,
  onThemeToggle,
}) => {
  const { t } = useTranslation();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const menuItems = [
    { key: "home", href: "/" },
    { key: "about", href: "/about" },
    { key: "contact", href: "/contact" },
  ];

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  return (
    <header
      className={`bg-white dark:bg-gray-900 shadow-sm border-b border-gray-200 dark:border-gray-700 ${className}`}
    >
      {/* Top Banner */}
      <div className="bg-primary-400 text-black px-4 py-2">
        <div className="container mx-auto flex items-center justify-center">
          <div className="flex items-center space-x-2">
            <Icon name="info" size="sm" />
            <span className="text-sm font-medium">{t("company.tagline")}</span>
          </div>
        </div>
      </div>

      {/* Main Header */}
      <div className="container mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          {/* Logo */}
          <div className="flex items-center">
            <Link to="/">
              {logo_image ? (
                <HexImage
                  hexData={logo_image}
                  imageType="png"
                  alt="Ceralume Logo"
                  className="cursor-pointer h-10 md:h-[45px] lg:h-16 w-auto"
                />
              ) : (
                <Logo variant="full" className="cursor-pointer h-10 md:h-[45px] lg:h-16 w-auto" />
              )}
            </Link>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-8">
            {menuItems.map((item) => (
              <Link
                key={item.key}
                to={item.href}
                className="text-gray-700 dark:text-gray-300 hover:text-primary-500 dark:hover:text-primary-400 font-medium transition-colors"
              >
                {t(`header.${item.key}`)}
              </Link>
            ))}
          </nav>

          {/* Right Side Actions */}
          <div className="flex items-center space-x-4">
            {/* Language Switcher */}
            <LanguageSwitcher />

            {/* Theme Toggle */}
            <Button
              variant="ghost"
              size="sm"
              className="p-2"
              onClick={onThemeToggle ?? (() => {})}
            >
              <Icon name={isDark ? "sun" : "moon"} size="sm" />
            </Button>

            {/* Mobile Menu Button */}
            <Button
              variant="ghost"
              size="sm"
              className="md:hidden p-2"
              onClick={toggleMobileMenu}
            >
              <Icon name={isMobileMenuOpen ? "close" : "menu"} size="sm" />
            </Button>
          </div>
        </div>

        {/* Mobile Navigation */}
        {isMobileMenuOpen && (
          <div className="md:hidden mt-4 pt-4 border-t border-gray-200 dark:border-gray-700">
            <nav className="flex flex-col space-y-3">
              {menuItems.map((item) => (
                <Link
                  key={item.key}
                  to={item.href}
                  className="text-gray-700 dark:text-gray-300 hover:text-primary-500 dark:hover:text-primary-400 font-medium transition-colors py-2"
                  onClick={() => setIsMobileMenuOpen(false)}
                >
                  {t(`header.${item.key}`)}
                </Link>
              ))}
            </nav>
          </div>
        )}
      </div>
    </header>
  );
};

export default Header;
