import React, { useState, forwardRef } from "react";

interface PasswordInputProps
  extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  error?: string;
  helpText?: string;
  fullWidth?: boolean;
  showToggle?: boolean;
  strengthIndicator?: boolean;
}

interface PasswordStrength {
  score: number;
  label: string;
  color: string;
}

const PasswordInput = forwardRef<HTMLInputElement, PasswordInputProps>(
  (
    {
      label,
      error,
      helpText,
      fullWidth = false,
      showToggle = true,
      strengthIndicator = false,
      className = "",
      disabled = false,
      value,
      onChange,
      ...props
    },
    ref,
  ) => {
    const [showPassword, setShowPassword] = useState(false);

    const togglePasswordVisibility = () => {
      setShowPassword(!showPassword);
    };

    const calculatePasswordStrength = (password: string): PasswordStrength => {
      let score = 0;
      if (!password)
        return { score: 0, label: "No password", color: "bg-gray-200" };

      // Length check
      if (password.length >= 8) score += 1;
      if (password.length >= 12) score += 1;

      // Character variety checks
      if (/[a-z]/.test(password)) score += 1;
      if (/[A-Z]/.test(password)) score += 1;
      if (/[0-9]/.test(password)) score += 1;
      if (/[^A-Za-z0-9]/.test(password)) score += 1;

      const strengthMap = {
        0: { score: 0, label: "Very Weak", color: "bg-red-500" },
        1: { score: 1, label: "Weak", color: "bg-red-400" },
        2: { score: 2, label: "Fair", color: "bg-yellow-400" },
        3: { score: 3, label: "Good", color: "bg-yellow-300" },
        4: { score: 4, label: "Strong", color: "bg-green-400" },
        5: { score: 5, label: "Very Strong", color: "bg-green-500" },
        6: { score: 6, label: "Excellent", color: "bg-green-600" },
      };

      return strengthMap[Math.min(score, 6) as keyof typeof strengthMap];
    };

    const strength =
      strengthIndicator && value
        ? calculatePasswordStrength(value as string)
        : null;

    const baseClasses =
      "px-3 py-2 border rounded-md text-sm transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed";

    const variantClasses = error
      ? "border-red-500 focus:border-red-500 focus:ring-red-500"
      : "border-gray-300 dark:border-gray-600 focus:border-primary-400 focus:ring-primary-400";

    const backgroundClasses =
      "bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 placeholder:text-gray-500 dark:placeholder:text-gray-400";

    const widthClasses = fullWidth ? "w-full" : "";

    const combinedClasses = `${baseClasses} ${variantClasses} ${backgroundClasses} ${widthClasses} pr-10 ${className}`;

    return (
      <div className={fullWidth ? "w-full" : ""}>
        {label && (
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
            {label}
          </label>
        )}
        <div className="relative">
          <input
            ref={ref}
            type={showPassword ? "text" : "password"}
            className={combinedClasses}
            disabled={disabled}
            value={value}
            onChange={onChange}
            {...props}
          />
          {showToggle && (
            <button
              type="button"
              className="absolute inset-y-0 right-0 pr-3 flex items-center"
              onClick={togglePasswordVisibility}
              disabled={disabled}
              tabIndex={-1}
            >
              {showPassword ? (
                <svg
                  className="h-4 w-4 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.878 9.878L3 3m6.878 6.878L21 21"
                  />
                </svg>
              ) : (
                <svg
                  className="h-4 w-4 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                  />
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                  />
                </svg>
              )}
            </button>
          )}
        </div>

        {strengthIndicator && strength && value && (
          <div className="mt-2">
            <div className="flex items-center space-x-2 mb-1">
              <div className="flex-1 bg-gray-200 rounded-full h-2">
                <div
                  className={`h-2 rounded-full transition-all duration-300 ${strength.color}`}
                  style={{ width: `${(strength.score / 6) * 100}%` }}
                />
              </div>
              <span className="text-xs font-medium text-gray-600 dark:text-gray-400 min-w-fit">
                {strength.label}
              </span>
            </div>
          </div>
        )}

        {error && (
          <p className="mt-1 text-sm text-red-600 dark:text-red-400">{error}</p>
        )}
        {helpText && !error && (
          <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
            {helpText}
          </p>
        )}
      </div>
    );
  },
);

PasswordInput.displayName = "PasswordInput";

export default PasswordInput;
