import React from "react";
import { useTranslation } from "react-i18next";
import { Button, Card, Icon } from "@/components";
import { Link } from "react-router-dom";
import { lazy } from 'react';

const LearningImage  = lazy(() =>  import ("../imageData/learning"));
const InternshipImage   = lazy(() =>  import ( "../imageData/internship"));
const ResearchImage   = lazy(() =>  import ( "../imageData/research"));


const Home: React.FC = () => {
  const { t } = useTranslation();

  const scrollToServices = () => {
    const element = document.getElementById("what-we-do");
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
  };

  const services = [
    {
      key: "upskilling",
      icon: "education",
    },
    {
      key: "internships",
      icon: "users",
    },
    {
      key: "research",
      icon: "brain",
    },
  ];

  return (
    <>
      {/* Hero Section */}
      <section className="relative bg-gradient-to-r from-gray-900 to-gray-800 dark:from-gray-900 dark:to-black text-white py-20">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto text-center space-y-6">
            <h1 className="text-5xl md:text-6xl font-bold leading-tight">
              {t("homepage.hero.title")}
            </h1>
            <p className="text-xl md:text-2xl text-gray-300 max-w-3xl mx-auto">
              {t("homepage.hero.subtitle 1")}
            </p>
            <p className="text-xl md:text-2xl text-gray-300 max-w-3xl mx-auto">
              {t("homepage.hero.subtitle 2")}
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center pt-6">
              <Button
                variant="primary"
                size="lg"
                onClick={scrollToServices}
                className="flex items-center space-x-2"
              >
                <Icon name="arrow-right" size="sm" />
                <span>{t("homepage.hero.cta")}</span>
              </Button>
            </div>
          </div>
        </div>
      </section>

      {/* Services Section */}
      <section id="what-we-do" className="py-16 bg-gray-50 dark:bg-gray-900">
        <div className="container mx-auto px-4">
          <div className="max-w-6xl mx-auto">
            <div className="text-center mb-12">
              <h2 className="text-4xl font-bold text-gray-900 dark:text-white mb-4">
                {t("homepage.services.title")}
              </h2>
              <p className="text-xl text-gray-600 dark:text-gray-400 max-w-2xl mx-auto">
                {t("company.slogan")}
              </p>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
              {services.map((service) => (
                <Card
                  key={service.key}
                  variant="elevated"
                  padding="none"
                  className="overflow-hidden"
                >
                  {
                    service.key === "upskilling" ? <LearningImage /> :
                    service.key === "internships" ? <InternshipImage /> :
                    service.key === "research" ? <ResearchImage /> :
                    <div className="w-full h-64 md:h-80 rounded-lg bg-gradient-to-br from-gray-100 to-gray-200 dark:from-gray-700 dark:to-gray-800 flex items-center justify-center">
                      <Icon
                        name="info"
                        size="xl"
                        className="text-gray-400 dark:text-gray-600"
                      />
                    </div>
}
                  <div className="p-6">
                    <div className="flex items-center space-x-3 mb-4">
                      <div className="w-12 h-12 md:w-14 md:h-14 lg:w-16 lg:h-16 bg-primary-400 rounded-lg flex items-center justify-center">
                        <Icon
                          name={service.icon}
                          size="md"
                          className="text-black dark:text-black md:w-6 md:h-6 lg:w-7 lg:h-7"
                        />
                      </div>
                      <h3 className="text-xl font-semibold text-gray-900 dark:text-white">
                        {t(`homepage.services.${service.key}.title`)}
                      </h3>
                    </div>
                    <p className="text-gray-600 dark:text-gray-400 mb-6">
                      {t(`homepage.services.${service.key}.description`)}
                    </p>
                    <ul className="space-y-2">
                      {(
                        t(`homepage.services.${service.key}.features`, {
                          returnObjects: true,
                        }) as string[]
                      ).map((feature: string, index: number) => (
                        <li
                          key={index}
                          className="flex items-center space-x-2 text-sm text-gray-700 dark:text-gray-300"
                        >
                          <Icon
                            name="check"
                            size="xs"
                            className="text-green-500 flex-shrink-0"
                          />
                          <span>{feature}</span>
                        </li>
                      ))}
                    </ul>
                  </div>
                </Card>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-16 bg-primary-400 text-black">
        <div className="container mx-auto px-4 text-center">
          <div className="max-w-4xl mx-auto space-y-6">
            <h2 className="text-4xl font-bold">{t("homepage.cta.title")}</h2>
            <p className="text-xl">{t("homepage.cta.subtitle")}</p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center pt-6">
              <Link to="/contact">
                <Button variant="secondary" size="lg">
                  {t("homepage.cta.button")}
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Home;
